            introduction: 'Introduccin',
            introLine1: `La pandemia de COVID-19 ha originado graves prdidas econmicas, por lo que se necesita un perodo sostenido de intervenciones, tanto de los gobiernos como del sector privado, para responder a la crisis y poder recuperarse. Por un lado, las micro, pequeas y medianas empresas (MPYMES) corren un alto riesgo de resultar severamente afectadas por los efectos generales de la epidemia y, por otra parte, estas empresas son las que estn encabezando la lucha por la supervivencia, no solo de sus propios negocios, sino tambin de los medios de vida en general, as como del bienestar de sus comunidades.`,
            introLine2: `Entre la serie de retos que enfrentan las MPYMES, el ms destacado es la falta de acceso al crdito. Por ejemplo, en el sur asitico, el 33% de estas empresas carece de acceso a prstamos y a una lnea de crdito. Por lo tanto, son ms vulnerables a los choques, en comparacin con las empresas ms grandes. En el caso de choques ms profundos, tal como el COVID-19, las MPYMES enfrentan un riesgo existencial debido a sus recursos ms limitados, tanto financieros como no financieros.`,
            introLine3: `La mayora de las MPYMES no tienen suficiente conocimiento especializado a nivel interno, ni acceso en sus reas para ayudarles a elaborar planes de continuidad empresarial y de recuperacin. Para apoyar a las MPYMES a que puedan alinear su razonamiento hacia la proteccin de sus negocios contra diversos riesgos, la herramienta propuesta para un clculo rpido del riesgo busca lograr lo siguiente:`,
